<template>
  <div id="app">
    <navi :navVal="activeNav"/>
    <router-view @changeNav="change"/>
  </div>
</template>
<script>
import navi from '@/components/navi.vue'
export default {
  data(){
    return{
      activeNav:{
        title:"电影",
        className:"movie"
      }
    }
  },
  components:{
    navi
  },
  methods: {
    change(obj){
      this.activeNav.title = obj.title;
      this.activeNav.className = obj.className;
    }
  },
}
</script>
